<template>
  <div class="cms-list-layout" v-loading="loading">
    <cms-prompt collapse="true">
      <li>显示系统保障服务及商家已加入的保障服务</li>
    </cms-prompt>
    <div style="padding:20px 0;min-height:400px;">
      <el-row :gutter="15">
        <el-col
          :span="8"
          :xs="24"
          :sm="24"
          :md="12"
          :lg="8"
          :xl="8"
          v-for="(table,index) in tableData"
          :key="index"
        >
          <div class="security-wrap">
            <div class="security-wrap__head">
              <el-button
                class="fr"
                size="mini"
                type="text"
                v-if="table.isOpen"
                @click="$routerLink('/store/security/view','cancel',{id:table.storeBondDetailId })"
              >取消服务</el-button>
              <el-button
                class="fr"
                size="mini"
                type="text"
                v-else
                @click="$routerLink('/store/security/view','open',{id:table.id})"
              >开通</el-button>
              <div class="security-wrap__image">
                <img class="security-wrap__img" :src="$cms.filterUrl(table.resourcesUrl)" alt>
              </div>
              <div class="security-wrap__cont">
                <h3 class="security-wrap__title">{{table.serviceName}}</h3>
                <p class="security-wrap__txt" v-if="table.isOpen">
                  <b class="t-ok iconfont icon-gou"></b>
                  <span>已加入</span>
                </p>
              </div>
            </div>
            <div class="security-wrap__txt indent">{{table.description}}</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { serviceList } from '@/api/shop/selfShops.js'
export default {
  mixins: [list],
  created() {
    this.getTableData(serviceList, {}, false)
  }
}
</script>

<style lang="scss" scoped>
.security-wrap {
  border: 1px solid rgba(219, 224, 229, 1);
  padding: 15px;
  box-shadow: 1px 1px 5px rgba(236, 236, 236, 1);
  margin-bottom: 15px;
  overflow: hidden;
  &__head {
    height: 80px;
  }
  &__image {
    width: 80px;
    height: 80px;
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    text-align: center;
    overflow: hidden;
  }
  &__cont {
    display: inline-block;
    vertical-align: top;
  }
  &__title {
    font-size: 16px;
    color: #1e1e1e;
    line-height: 40px;
    letter-spacing: 1px;
  }
  &__txt {
    font-size: 13px;
    color: #666;
    line-height: 20px;
    margin-top: 15px;
  }

  &__img {
    width: 100%;
    height: 100%;
  }
}
.indent {
  text-indent: 1em;
  height: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}
.t-ok {
  font-size: 18px;
  font-weight: bold;
  color: #008000;
  position: relative;
  top: 2px;
}
.security-wrap__image {
  background-color: #f7f7f7;
}
</style>




