// 自营商店相关接口
import axios from '../fetch'

// 保障服务列表
export const serviceList = params => { return axios.get('/admin/store/sysconsumerservice/list', { params: params }) }

// 保障服务详情（未加入）
export const serviceSaveView = params => { return axios.get('/admin/store/sysconsumerservice/toSaveService', { params: params }) }

// 保障服务详情（已加入）
export const serviceCancelView = params => { return axios.get('/admin/store/sysconsumerservice/toCancelService', { params: params }) }

// 保障服务-取消服务
export const cancelService = params => { return axios.post('/admin/store/sysconsumerservice/cancelService', params) }

// 保障服务-开通服务
export const servicePay = params => { return axios.post('/admin/store/pay', params) }




